@import 'styles/variables';
@import 'styles/breakpoints';

$pastas: #d73b3b;
$steaks: #30201e;
$combos: #e6c864;
$vegans: #80aa40;
.tags {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: 20px;
	max-width: 700px;
	@media (max-width: $desktop_xsm) {
		justify-content: flex-end;
	}
	@media (max-width: $mobile) {
		justify-content: flex-start;
	}
	div {
		margin: 10px;
	}
	&__type {
		align-items: center;
		border-radius: 2px;
		display: flex;
		font-weight: bold;
		height: 40px;
		justify-content: center;
		padding: 10px 30px;
		&__pastas {
			background-color: $pastas;
			color: white;
		}
		&__steaks {
			background-color: $steaks;
			color: white;
		}
		&__combos {
			background-color: $combos;
		}
		&__vegans {
			background-color: $vegans;
		}
	}
	&__portion,
	&__qtypeople,
	&__cost {
		align-items: center;
		display: flex;
		font-size: 1.25rem;
		font-weight: bold;
		justify-content: center;
	}
	&__cost {
		color: $red;
		font-size: 1.7rem;
	}
}
