@import 'styles/variables';

.suggestions {
	border-radius: 2px;
	display: flex;
	flex-wrap: wrap;
	gap: 30px;
	justify-content: space-between;
}
.suggested {
	margin-bottom: 100px;
	&__img {
		width: 300px;
		margin-bottom: 10px;
		img {
			border-radius: 8px;
			width: 100%;
		}
	}
	&__btn {
		background-color: $red;
		border: none;
		border-radius: 5px;
		color: white;
		cursor: pointer;
		font-size: 1.2rem;
		font-weight: bold;
		padding: 10px 0;
		transition: 0.2s ease;
		width: 100%;
		&:hover {
			background-color: $red-dark;
		}
	}
}
.ourPlace {
	margin-bottom: 100px;
	position: relative;
	width: 100%;
	img {
		border-radius: 5px;
		width: 100%;
	}
	&__address {
		align-items: center;
		background-color: $dark;
		border-radius: 5px;
		bottom: -45px;
		color: white;
		display: flex;
		flex-direction: column;
		font-weight: bold;
		height: 100px;
		justify-content: center;
		position: absolute;
		margin: 0 auto;
		left: 0;
		right: 0;
		width: 200px;
	}
}
