@import 'styles/variables';
@import 'styles/breakpoints';

.menu {
	padding: 20px $x-padding;
	display: flex;
	&__list {
		align-items: center;
		display: flex;
	}
	&__link {
		list-style-type: none;
		padding: 0 20px;
		a {
			color: $dark;
			cursor: pointer;
			font-size: 24px;
			font-weight: 600;
			text-decoration: none;
			transition: 0.2s ease;
			&:hover {
				color: $dark-grey;
			}
		}
	}
	@media (max-width: $desktop_xsm) {
		padding: 20px $x-padding-mobile;
	}

	@media (min-width: $desktop_sm) {
		padding: 20px $x-padding-sm;
	}
	@media (min-width: $desktop_sm) and (max-width: $desktop_md) {
		padding: 20px $x-padding-md;
	}
	@media (min-width: $desktop_md) {
		padding: 20px $x-padding;
	}
}
