@import 'styles/variables';
.container {
	align-items: center;
	display: flex;
	flex: 1;
	flex-direction: column;
	justify-content: center;
}
.return {
	display: flex;
	justify-content: flex-start;
	width: 100%;
	button {
		background-color: transparent;
		border: none;
		color: $red;
		cursor: pointer;
		font-size: 1.5rem;
		font-weight: bold;
	}
}
